.styleguide {
  background: #333; }
  .styleguide main {
    color: #eee; }
  .styleguide .overlay {
    background: #f8e7ff; }
    .styleguide .overlay .container {
      height: calc(100vh - 2.75rem); }
    .styleguide .overlay .block {
      text-align: center; }
  .styleguide .sg-nav-overlay {
    width: auto;
    display: block; }
    .styleguide .sg-nav-overlay .icon-button {
      text-align: left; }
  .styleguide .sg-nav {
    padding-top: 16px;
    padding-top: 1rem; }
    .styleguide .sg-nav .sg-nav-item {
      display: block;
      padding: 10.4px 16px;
      padding: 0.65rem 1rem;
      font-size: 19.2px;
      font-size: 1.2rem; }
      .styleguide .sg-nav .sg-nav-item:hover, .styleguide .sg-nav .sg-nav-item:focus {
        background: #4e86ff;
        color: #fdfdfd; }
      .styleguide .sg-nav .sg-nav-item.active {
        background: #c84eff;
        color: #fdfdfd; }

.sg-grid .sg-grid-example {
  margin-bottom: 48.54px;
  margin-bottom: 3.03375rem; }

.sg-grid .container.sg-example:nth-child(1) {
  background: #ff664e; }

.sg-grid .container.sg-example:nth-child(2) {
  background: #ff924e; }

.sg-grid .container.sg-example:nth-child(3) {
  background: #ffc74e; }

.sg-grid .container.sg-example:nth-child(4) {
  background: #92ff4e; }

.sg-grid .container.sg-example:nth-child(5) {
  background: #4ee7ff; }

.sg-grid .container.sg-example:nth-child(6) {
  background: #4e86ff; }

.sg-grid .container.sg-example:nth-child(7) {
  background: #c84eff; }

.sg-grid .container.sg-example:nth-child(8) {
  background: #ff4ee2; }

.sg-grid .container.sg-example .block {
  min-height: 30px;
  min-height: 1.875rem;
  text-align: center; }
  .sg-grid .container.sg-example .block:nth-child(odd) {
    background: rgba(0, 0, 0, 0.2); }
  .sg-grid .container.sg-example .block:nth-child(even) {
    background: rgba(0, 0, 0, 0.4); }

.sg-colors .colors,
.sg-colors .colors-greyscale {
  margin-bottom: 48.54px;
  margin-bottom: 3.03375rem; }

.sg-colors .colors {
  color: #222; }
  .sg-colors .colors .block:nth-child(1) {
    background: #ff664e; }
  .sg-colors .colors .block:nth-child(2) {
    background: #ff924e; }
  .sg-colors .colors .block:nth-child(3) {
    background: #ffc74e; }
  .sg-colors .colors .block:nth-child(4) {
    background: #92ff4e; }
  .sg-colors .colors .block:nth-child(5) {
    background: #4ee7ff; }
  .sg-colors .colors .block:nth-child(6) {
    background: #4e86ff; }
  .sg-colors .colors .block:nth-child(7) {
    background: #c84eff; }
  .sg-colors .colors .block:nth-child(8) {
    background: #ff4ee2; }

.sg-colors .colors-greyscale .block:nth-child(1) {
  background: #222; }

.sg-colors .colors-greyscale .block:nth-child(2) {
  background: #333; }

.sg-colors .colors-greyscale .block:nth-child(3) {
  background: #5e5e5e; }

.sg-colors .colors-greyscale .block:nth-child(4) {
  background: silver;
  color: #222; }

.sg-colors .colors-greyscale .block:nth-child(5) {
  background: #e5e5e5;
  color: #222; }

.sg-colors .colors-greyscale .block:nth-child(6) {
  background: #eee;
  color: #222; }

.sg-colors .colors-greyscale .block:nth-child(7) {
  background: #fdfdfd;
  color: #222; }

.sg-colors .color-swatch {
  height: 80px;
  height: 5rem; }
  .sg-colors .color-swatch figcaption {
    text-align: center;
    padding-top: 32px;
    padding-top: 2rem; }
